import {connect} from 'react-redux';
import { bindActionCreators } from 'redux';
import * as mapActions from '../../actions/mapAction';
import React, {Component} from 'react';
import Grid from './parts/Grid';


class Map extends Component{
    constructor(props){
        super(props);

        let clientWidth = document.documentElement.clientWidth;
        let clientHeight = document.documentElement.clientHeight;



        this.state = {
            countColumn: Math.floor(clientHeight / 50),
            countRows: Math.floor(clientWidth / 50),
            gridArray: [],
            test: false,

        };
    }

    componentWillMount(){
        this.setState({
            gridArray: this.createGridArray(),
            test: true
        });
    }

    shouldComponentUpdate(nextProps){
        this.setState({
            gridArray: nextProps.gridArray,
        });
        console.log(nextProps,'should');
        return true;
    }

    createGridArray(rows = this.state.countRows, column = this.state.countColumn){
        let array = [];
        for(let i = 0;i < column;i++){
            array[i] = [];
            for(let j = 0;j < rows;j++){
                array[i][j] = i;
            }
        }
        const { initMap } = this.props.mapActions;
        initMap(array);
        return array;
    }

    clickHandle(){
        console.log(this.props.map, 'MAP HANDLECLICK');
    }

    render(){

        const map = this.state.gridArray;
        const mapActions = this.props.mapActions;
        return(
            <div className="map" onClick={this.clickHandle.bind(this)}>
                <Grid gridArray={map} mapActions = {mapActions}/>
            </div>
        );
    }
}

function mapStateToProps (state) {
    return {
        map: state.mapReducer,
    }
}

function mapDispatchToProps(dispatch) {
    return{
        mapActions: bindActionCreators(mapActions, dispatch)
    };
}


export default connect(mapStateToProps, mapDispatchToProps)(Map);